@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');

.about {
  font-size: 2vw;
  font-family: 'Inter', sans-serif;
  color: white;
}

.introduction {
  font-size: 5vw;
  font-family: 'Inter', sans-serif;
  color: white;
}

.subIntroduction {
  font-size: 3vw;
  font-family: 'Inter', sans-serif;
  color: white;
}

body {
  font-family: 'Inter', sans-serif;
 }

.gradient {
  background: -webkit-linear-gradient(250deg, #D0F978, #7ABF9A);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.reactIcons {
  color: white;
}

.aboutPage {
  display: flex;
  flex-direction: row;
  min-width: 100px;
}

@media only screen and (max-width: 1000px) {
  .aboutPage {
    flex-wrap: wrap;
  }
}